/* Base style */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  color: #3276B1;
}

.container{
  max-width: 1140px;
  margin: auto;
}
.header{
  background-color: #222222;
  color: white;
  width: 100%;
  padding: 10px;
}
.flex{
  display: flex;
  justify-content: space-between;
}
.header-left{
  font-size: 20px;
}
.header-right{
  background-color: rgb(50, 205, 140);
  color: white;
  padding: 10px 18px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}
/* HEro section */
.hero-section{
  background-color: #EEEEEE;
  color: rgb(51, 51, 51);
}
.hero{
  padding: 70px 0;
}
.hero-title{
  font-size: 50px;
  margin-bottom: 10px;
}
.hero-pera{
  font-size: 23px;
  margin-top: 10px;
  margin-bottom: 16px;
}
.hero-btn{
  padding: 10px 18px;
  background-color: #3276B1;
  color: white;
  border-radius: 5px;
  border: none;
  font-size: 18px;

}
/* Main section */
.main{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 50px auto;
  
}
.main-left{
  width: 70%;
  padding: 40px;
}
.main-right{
  width: 30%;
  background-color: rgb(235, 235, 235);
  border-radius: 5px;
  padding: 30px;
  border: 1px solid rgb(197, 197, 197);
}
.main-right ul{
  padding: 20px;
}
.main-right h2{
  padding-top: 10px;
}
.card{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 10px;
  margin-top: 30px;
}
.single-card{
  background-color: rgb(146, 188, 204);
  padding:20px;
  border-radius: 6px;
}
.single-card button{
  background-color: rgb(196, 101, 84);
  padding: 7px 15px;
  border: none;
  color: white;
  font-size: 18px;
  border-radius: 10px;
  display: inline-block;
  margin-top: 20px;
}

@media only screen and (max-width: 600px){
	.main{
    flex-direction: column; 
  }
  .card{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    width: 100%;

  }
 
  .main-left{
    width: 100%;
    padding: 10px;
  }
  .main-right{
    width: 100%;
    
  }
  .hero{
    padding: 70px 20px;
  }
  .hero-title{
    font-size: 22px;
  }
  
}
